@import "colors";
@import "fonts";

.comment-container {
    width: 100%;
    height: 100%;
    background: linear-gradient(180deg, $gray 0%, $gray 50%, $beige 50%);
    padding-top: $nav-height;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1rem;
    z-index: -1;
    box-shadow: 0px 5px 5px 0px #00000088 inset;
}

#comment-capsule {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
}

.carousel-root {
    width: 70% !important;
    height: calc(100% - 60px) !important;
    display: flex !important;
    flex-direction: column !important;
  }
  
  .carousel-slider {
    height: 100% !important;
  }

  .carousel .slide {
    color: black;
    height: 80% !important;
  }
  
  .carousel .slide img {
    width: 40% !important;
    border-radius: 50%;
  }

  .carousel .slide .selected {
    width: 100%;
    height: 100% !important;
    margin: auto !important;
  }
  
  .slider-wrapper {
    height: 100% !important;
    display: flex !important;
    justify-content: center !important;
    align-items: center !important;
  }

  .slider {
    height: 100% !important;
  }

  .myCarousel {
    background: #EEEEEE;
    width: 50%;
    margin: 1rem auto;
    height: 100%;
    box-shadow: 5px 5px 5px 5px #00000088;
    padding: 2rem;
    display: flex;
    gap: 0.5rem;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-family: $Poppins-font;
  }
  
  .carousel .control-dots {
    padding-left: 5px !important;
    outline: 0;
    bottom: 5% !important;
  }
  
  .myCarousel h3 {
    color: #222;
    letter-spacing: 0.2px;
    font-weight: 600;
    text-transform: uppercase;
    font-size: 1.5rem;
  }
  
  .myCarousel h4 {
    text-transform: uppercase;
    font-weight: 500;
    color: #787878;
    font-size: 1.2rem;
  }
  
  .myCarousel p {
    font-weight: 100 !important;
    color: #222;
    font-size: 1.5rem;
    height: 100% !important;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .carousel .control-dots .dot {
    box-shadow: none !important;
    background: #454545 !important;
    outline: 0;
  }
  
  .carousel.carousel-slider .control-arrow {
    background: #222 !important;
    width: 50px !important;
    height: 100px !important;
    position: absolute;
    top: calc(50% - 82px) !important;
  }

  @media only screen and (max-width: 934px) {
    .carousel-root {
      outline: 0;
      width: 93% !important;
      margin: auto !important;
    }
  
    .carousel.carousel-slider .control-arrow {
      display: none !important;
    }
    .myCarousel {
      background: #fafafa;
      margin-top: -9%;
      width: 88%;
      margin-left: auto;
      margin-right: auto;
      padding-top: 8%;
      padding-bottom: 12.5%;
      padding-left: 5%;
      padding-right: 5%;
      border: 1px solid #ddd;
      height: 269px;
    }
  
    .carousel .slide img {
      width: 24% !important;
      border-radius: 50%;
    }
  }
