@import 'fonts';
@import 'colors';
@import 'animations';


//DESKTOP RESPONSIVE
@media only screen and (max-width : 1600px) {
    #home {
        animation: bgslider 20s linear 0s infinite;
        background-size: 110%;
    }

    #header-title-container {
        padding: 0.5rem 0;

        h1 {
            font-size: 4rem;
            margin-bottom: 0.25rem;
        }

        h2{
            font-size: 1.2rem;
        }
    }
}

//LAPTOP RESPONSIVE
@media only screen and (max-width : 1200px) {
    #home {
        animation: bgslider 25s linear 0s infinite;
        background-size: cover;
    }

    #header-title-container {
        padding: 0.25rem 0;

        h1 {
            font-size: 6rem;
            margin-bottom: 0.25rem;
        }

        h2{
            font-size: 1.75rem;
        }
    }
}

//TABLET RESPONSIVE
@media only screen and (max-width : 768px) {
    #home {
        animation: bgslider 25s linear 0s infinite;
        background-size: cover;
    }

    #header-title-container {
        padding: 0.5rem 0;

        h1 {
            font-size: 3.25rem;
            margin-bottom: 0.25rem;
        }

        h2{
            font-size: 1.25rem;
        }
    }
}

//SMARTPHONE RESPONSIVE
@media only screen and (max-width : 320px) {
    #home {
        animation: bgslider 25s linear 0s infinite;
        background-size: cover;
    }

    #header-title-container {
        padding: 0.5rem 0;

        h1 {
            font-size: 3rem;
            margin-bottom: 0.5rem;
        }

        h2{
            font-size: 1.25rem;
        }
    }
}

#home {
    width: 100%;
    height: 100%;
    background-image: url('https://miro.medium.com/max/1200/1*cY8vEbWdbbc0ZyPDdRuCIg.jpeg');
    background-repeat: no-repeat;
    background-position-y: center;
    background-attachment: fixed;
    animation-direction: alternate-reverse;
    z-index: -1;
    box-shadow: 0px 5px 5px 0px #00000088;
}

.blur {
    width: 100%;
    height: 100%;
    background-color: #00000044;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

#header-title-container {
    background-color: $beige-transparent;
    text-align: center;
    width: 75%;
    max-width: 600px;
    display: none;
    font-family: $Poppins-font;
    padding: 1rem;
    border-radius: 0.2rem;

    h1 {
        font-weight: 900;
        margin-bottom: 1rem;
    }

    h2 {
        font-size: 1.25rem;
    }
}

.share-buttons {
    min-width: 250px;
    width: 30%;
    max-width: 300px;
    height: 60px;
    position: absolute;
    right: 2rem;
    bottom: 2rem;
    display: flex;
    justify-content: space-between;
    align-items: center;

    .share-button {
        background-color: $peach;
        width: calc(20% - 0.2rem);
        margin: 0 0.1rem;
        height: 80%;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 2rem;
        color: $white;
        border-radius: 0.5rem;
        transition: transform .4s ease, background-color .4s ease;
        cursor: pointer;
        text-decoration: none;
    }
    .share-button:hover {
        transform:translateY(-10px);
    }

    #facebook-button {
        background-color: $facebook-hbtn;
    }
    #instagram-button {
        background-color: $instagram-hbtn;
    }
    #youtube-button {
        background-color: $youtube-hbtn;
    }
    #twitter-button {
        background-color: $twitter-hbtn;
    }
    #linkedin-button {
        background-color: $linkedin-hbtn;
    }

    #facebook-button:hover {
        background-color: $facebook-btn;
    }
    #instagram-button:hover {
        background-color: $instagram-btn;
    }
    #youtube-button:hover {
        background-color: $youtube-btn;
    }
    #twitter-button:hover {
        background-color: $twitter-btn;
    }
    #linkedin-button:hover {
        background-color: $linkedin-btn;
    }
}