@import 'colors';
@import 'fonts';
@import 'audio-player-bar';

#law-capsule {
    height: calc(100% - $nav-height);
    padding: 1rem;
    padding-top: calc(60px + 0.5rem);
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 1rem;
    box-shadow: 0px 5px 5px 0px #00000088 inset;
    font-family: $Poppins-font;
}


#audio-track-selector {
    min-width: 200px;
    width: 30%;
    max-width: 400px;
    height: 100%;
    padding: 0.5rem;
    background-color: $beige;
    box-shadow: 2px 2px 2px 2px #00000088;
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
    overflow-y: scroll;

    .track-id {
        width: 100%;
        min-height: 20%;
        display: flex;
        background-color: $beige;
        box-shadow: 1px 1px 1px 1px #00000088;

        .track-img {
            width: 30%;
            height: 100%;
            background-color: $red;
            display: flex;
            align-items: center;
            justify-content: center;
            font-size: 2rem;
            color: $white;
            cursor: pointer;
        }

        .track-list {
            width: 70%;
            height: 100%;
            padding: 0.5rem;
            display: flex;
            flex-direction: column;
            justify-content: space-between;

            .track-list-title {
                text-align: center;
                text-align: 1rem;
            }

            .track-list-description {
                text-align: justify;
                text-align: 0.8rem;
            }

            .track-upload-date {
                font-size: 0.8rem;
                text-align: right;
            }
        }
    }

    &::-webkit-scrollbar {
        width: 5px;
        background-color: $beige;
    }

    &::-webkit-scrollbar-thumb {
        background-color: $peach;
    }
}

#audio-track-block {
    width: 70%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: column;
    row-gap: 1rem;

    #audio-track-description {
        width: 100%;
        height: 80%;
        background-color: $beige;
        box-shadow: 5px 5px 5px 5px #00000088;
        padding: 1rem;
        overflow-y: scroll;
        text-align: justify;

        &::-webkit-scrollbar {
            width: 5px;
            background-color: $peach;
        }
    
        &::-webkit-scrollbar-thumb {
            background-color: $red;
        }
    }

    #audio-track-control {
        width: 100%;
        height: 20%;
        box-shadow: 0px 5px 5px 0px #00000088;
    }
}




