@import 'colors';
@import 'fonts';

.footer {
  width: 100%;
  padding: 0 1rem;
  height: 20vh;
  background-color: $beige;
  display: flex;
  align-items: center;
  justify-content: space-around;
  font-size: 0.9rem;
  flex-direction: column;
  box-shadow: 5px 5px 5px 5px #00000088;
}

#footer-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: calc(100% - 1rem);
  height: calc(50% - 2.5px);
}

hr {
  height: 2px;
  width: 100%;
  background-color: $red;
  border: none;
}

.footer-item {
  color: $black;
  font-family: $Poppins-font;
  font-size: 1rem;
}

#authors-footer {
  text-align: right;
  height: calc(50% - 2.5px);
  display: flex;
  align-items: center;
  justify-content: center;
}