@import 'fonts';
@import 'colors';

$scrollbar-width: 0px;

* {
    margin: 0;
    box-sizing: border-box;
    cursor: default;
}

#html {
    font-size: $font-size;
    scroll-behavior: smooth;
}

#body {
    width: calc(100vw - $scrollbar-width);
    height: 100%;
    overflow-x: hidden;
}

::-webkit-scrollbar {
    width: $scrollbar-width;
    background-color: $gray-color02;
}

::-webkit-scrollbar-thumb {
    background-color: $gray-color04;
    transition: background-color .4s ease;
}

::-webkit-scrollbar-thumb:hover {
    background-color: $gray-color05;
}

::-webkit-scrollbar-thumb:active {
    background-color: $gray-color06;
}

#root {
    width: 100%;
}

#header {
    width: 100%;
    height: 100vh;
}

#main {
    width: 100%;
    height: 100%;
}

.main-section {
    width: 100%;
    height: 100vh;
}