@import "colors";
@import "fonts";

#contacts {
  width: 100%;
  height: calc(80vh);
  padding: 0 1rem;
  padding-top: calc(60px + 1rem);
  padding-bottom: 1rem;
  box-shadow: 0px 5px 5px 0px #00000088 inset;
  font-family: $Poppins-font;
}

.contacts-container {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;

  .form-contact-container {
    width: 50%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    padding: 1rem;
    box-shadow: 2px 2px 2px 2px #00000088;
    gap: 1rem;

    h3 {
      font-size: 1.5rem;
      text-align: center;
    }

    .contact-form {
      width: 60%;
      height: 60%;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      box-shadow: 1px 1px 1px 1px #00000088;
      padding: 1rem;

      label {
        text-align: left;
        width: 80%;
        max-width: 300px;
        padding: 0 1rem;
      }

      .contact-form-input {
        width: 80%;
        max-width: 300px;
        height: 2rem;
        border: none;
        margin-bottom: 1.5rem;
        padding: 0.2rem;
        border-bottom: 1px solid $black;
        outline: none;
        font-family: $Poppins-font;
        background-color: transparent;

        &:active {
          outline: none;
          background-color: transparent;
        }

        
      }

      button {
        font-family: $Poppins-font;
        font-size: 1rem;
        border-radius: 0.2rem;
        padding: 0.5rem 1rem;
        border: none;
        background-color: transparent;
        box-shadow: 2px 2px 2px 2px #00000088;
      }
    }
  }
}