@import 'colors';
@import 'fonts';
@import '~react-image-gallery/styles/scss/image-gallery.scss';

@mixin flex($js-content, $align-items, $rc) {
    display: flex;
    justify-content: $js-content;
    align-items: $align-items;
    flex-direction: $rc;
}

@mixin block($w, $h) {
    width: $w;
    height: $h;
}

.gallery-container {
    @include block(100%, 100%);
    padding: 1rem;
    padding-top: calc(60px + 0.5rem);
    box-shadow: 0px 5px 5px 0px #00000088 inset;
    display: flex;
    align-items: center;
    gap: 1rem;
}

.image-gallery {
    width: 60%;
    height: 100%;
    padding: 0.5rem;
    box-shadow: 5px 5px 5px 5px #00000088;
    overflow: hidden;
    object-fit: scale-down;
}

.image-gallery-slide img {
    width: 100%;
    height: 450px;
}

.fullscreen .image-gallery-slide img {
    max-height: 100vh;
}

.gallery-description {
    width: 40%;
    height: 100%;
    padding: 1rem;
    box-shadow: 5px 5px 5px 5px #00000088;
    display: flex;
    gap: 0.5rem;
    justify-content: space-between;
    flex-direction: column;
    font-family: $Poppins-font;

    #img-title {
        font-size: 2rem;
        text-align: center;
    }
    
    #img-desc {
        height: 100%;
        overflow-y: scroll;
        text-align: justify;
        padding: 0 0.5rem;
        text-indent: 1rem;

        &::-webkit-scrollbar {
            width: 2px;
        }
    }

    #img-date {
        text-align: right;
    }
}