@import 'fonts';
@import 'colors';

@mixin flex($js-content, $align-items, $rc) {
  display: flex;
  justify-content: $js-content;
  align-items: $align-items;
  flex-direction: $rc;
}

@mixin block($w, $h) {
  width: $w;
  height: $h;
}

#nav {
  @include flex(space-between, auto, auto);
  @include block(100%, 60px);
  max-width: 100%;
  background-color: $beige;
  position: fixed;
  z-index: 9999;

  #nav-banner {
    @include flex(space-between, center, auto);
    @include block(auto, 100%);
    gap: 0.75rem;
    margin-left: 0.5rem;

    .fa-solid {
      font-size: 45px;
    }

    h3 {
      font-size: 30px;
      font-family: $Poppins-font;
    }
  }

  #nav-list {
    @include flex(center, center, auto);
    @include block(auto, 100%);
    list-style: none;
    padding: 0;

    .nav-item {
      @include flex(center, center, auto);
      @include block(100%, 100%);

      .nav-link {
        @include flex(center, center, auto);
        @include block(100%, 100%);
        color: $black;
        padding: 0 1rem;
        text-decoration: none;
        font-family: $Open-Sans-font;
        text-align: center;
        transition: background-color 0.4s ease;

        &:hover {
          background-color: $gray;
        }
      }

      .nav-link-fa {
        display: none;
      }
    }
  }
}
